import Axios from 'axios'
import { API_URL, API_TIMEOUT } from '@/config'
import resetMessage from '@/utils/resetMessage'
import * as types from '../store/mutation-types'
// // // 获取token
import KLStorage from '@/utils/storage'
import API from '@/api'
const CancelToken = Axios.CancelToken
// import router from '@/router'
import VueBUS from './EventBus'
VueBUS.$httpRequestList = []
// 创建Axios实例
const service = Axios.create({
  baseURL: API_URL, // 后台 api 的 url
  timeout: API_TIMEOUT, // 请求超时时间,
  validateStatus(status) {
    switch (status) {
      case 400:
        resetMessage.error('请求出错')
        break
      case 401:
        resetMessage.warning({
          message: '授权失败，请重新登录'
        })
        return
      case 412:
        resetMessage.warning({
          message: '授权失败，请重新登录'
        })
        return
      case 403:
        resetMessage.warning({
          message: '拒绝访问'
        })
        break
      case 404:
        resetMessage.warning({
          message: '请求错误,未找到该资源'
        })
        break
      case 500:
        resetMessage.warning({
          message: '服务端错误'
        })
        break
    }
    return status >= 200 && status < 300
  }
})

service.interceptors.request.use(
  config => {
    config.cancelToken = new CancelToken(c => {
      // console.log(this.$httpRequestList);
      VueBUS.$httpRequestList.push(c)
    })
    // Do something before request is sent
    if (KLStorage.getItem(types.TOKEN)) {
      config.headers['x-auth-token'] = KLStorage.getItem(types.TOKEN)
    }
    config.headers.roleNo = 1
    return config
  },
  error => Promise.reject(error)
)

// Add a response interceptor
service.interceptors.response.use(
  response => {
    // Do something with response data
    if (
      response.config.responseType === 'arraybuffer' ||
      response.headers['content-type'] == 'application/zip'
    ) {
      //主要针对导出下载
      return response
    }
    if (response.data.status === 401) {
      resetMessage.warning({
        message: 'token失效，请重新登录'
      })
      setTimeout(() => {
        API.getPlatformAppkey({ appKey: 'ED7FE621710E41F2A01244C0EEDC9F9D' }).then(res => {
          if (res.status === 200) {
            let result = interceptStr(res.data.url, '/', 3, '')
            localStorage.removeItem('token')
            window.location.href =
              result +
              'authorization?redirect=' +
              location.href +
              '&appKey=5561F007A0D14AED96096437F0C2C798'
          } else {
            this.$message.error(res.error)
          }
        })
        // window.location.href = 'http://10.10.1.8:8081/login?appKey=osmagic-access-platform' //本地环境
        // // window.location.href = 'http://192.168.2.27:30500/login?appKey=osmagic-access-platform' //测试环境地址
        // // window.location.href = 'http://10.10.8.5:30500/login?appKey=osmagic-access-platform' //开发环境地址
      }, 1000)
    }
    if (response.data.status !== 200) {
      resetMessage.error({
        message: response.data.message
      })
    }
    return response.data
  },
  error => {
    error.request && error.request.readyState === 4 && console.error('network error')
    // Do something with response error
    return Promise.reject(error)
  }
)
function interceptStr(str, ele, index, url) {
  let num = str.indexOf(ele) + 1
  if (index <= 1) {
    url = url + str.substring(0, num)
    str = str.substring(num)
    return url
  }
  url = url + str.substring(0, num)
  str = str.substring(num)
  index--
  return interceptStr(str, ele, index, url)
}

export default service
