import Http from '@/utils/http'

export default {
  getMenuList: params =>
    Http.get('/access/api/v1/menus', {
      params
    }),
  getMenuListAsc: params =>
    Http.get('/access/api/v1/menus?orders%5B0%5D.asc=false', {
      params
    }),
  getMenuAdd: data => Http.post('/access/api/v1/menus', data),
  getMenuDetail: params => Http.get(`/access/api/v1/menus/${params.id}`, { params }),
  getMenuHide: params => Http.get(`/access/api/v1/menus/hide/${params.id}`, { params }),
  getMenuDelete: data => Http.post('/access/api/v1/menus/delete', data),
  getMenuModify: data => Http.post(`/access/api/v1/menus/${data.id}`, data)
}
