import Http from '@/utils/http'

export default {
  getRolesList: params =>
    Http.get('/access/api/v1/roles?orders%5B0%5D.asc=false&orders%5B0%5D.column=createTime', {
      params
    }),
  getRolesDelete: data => Http.post('/access/api/v1/roles/delete', data),
  getRolesAdd: data => Http.post('/access/api/v1/roles', data),
  getRolesMotiy: data => Http.post(`/access/api/v1/roles/${data.id}`, data),
  getRolesAuth: data => Http.post(`/access/api/v1/roles/auth`, data),
  getRolesMenuTree: params => Http.get(`/access/api/v1/menus/List`, { params }),
  getRolesAll: params => Http.get(`/access/api/v1/roles/all`, { params })
}
