import Http from '@/utils/http'

export default {
  // 文件上传
  uploadFile: data => Http.post('/theia/api/v1/fs/store', data),
  //在线用户
  getAccoutOnline: params => Http.get('/access/api/v1/accounts/online', { params }),
  getAccoutCenter: params => Http.get('/access/api/v1/accounts/center', { params }),
  // 用户注销
  logout: data => Http.post('/access/api/v1/logout', data),
  // 系统设置
  getSystem: params => Http.get('/access/api/v1/system-confs', { params }),
  getSystemEditor: data => Http.post(`/access/api/v1/system-confs/${data.id}`, data)
}
