<template>
  <div>
    <div class="user-icon-container flex-col flex-alignC">
      <img src="../../../assets/img/admin-tx.png" onerror="" />
      <el-dropdown @command="logout" trigger="click">
        <span class="el-dropdown-link">
          {{ username }}
          <i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <!-- <el-dropdown-item command="person">个人中心</el-dropdown-item> -->
          <el-dropdown-item command="logout">退出登录</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import API from '@/api'
export default {
  data() {
    return {
      username: ''
    }
  },
  created() {
    this.getCenter()
  },
  methods: {
    logout() {
      API.logout().then(res => {
        if (res.status === 200) {
          this.platformDetail()
        } else {
          this.$message.error(res.error)
        }
      })
    },
    platformDetail() {
      API.getPlatformAppkey({ appKey: 'ED7FE621710E41F2A01244C0EEDC9F9D' }).then(res => {
        if (res.status === 200) {
          localStorage.removeItem('token')
          window.location.href = res.data.url
          // window.location.href = 'http://10.10.1.38:8081/authorization'
        } else {
          this.$message.error(res.error)
        }
      })
    },
    getCenter() {
      API.getAccoutCenter().then(res => {
        if (res.status == 200) {
          this.username = res.data.account
        } else {
          this.username = '未知用户'
          this.$message.error(res.message)
        }
      })
    }
  }
}
</script>

