import Http from '@/utils/http'

export default {
  getPlatformList: params =>
    Http.get('/access/api/v1/platforms/all', {
      params
    }),
  getPlatformListPage: params =>
    Http.get('/access/api/v1/platforms?orders%5B0%5D.asc=false&orders%5B0%5D.column=createTime', {
      params
    }),
  getPlatformAdd: data => Http.post('/access/api/v1/platforms', data),
  getPlatformModify: data => Http.post(`/access/api/v1/platforms/${data.id}`, data),
  getPlatformDelete: data => Http.post(`/access/api/v1/platforms/delete`, data),
  getPlatformDetail: params =>
    Http.get(`/access/api/v1/platforms/${params.id}`, {
      params
    }),
  getPlatformAppkey: params =>
    Http.get(`/access/api/v1/platforms/data/${params.appKey}`, {
      params
    })
}
