import Http from '@/utils/http'

export default {
  getAcountList: params =>
    Http.get('/access/api/v1/accounts?orders%5B0%5D.asc=false&orders%5B0%5D.column=createTime', {
      params
    }),
  getAccountAdd: data => Http.post(`/access/api/v1/accounts`, data),
  getAccountMotiy: data => Http.post(`/access/api/v1/accounts/${data.id}`, data),
  getAccountDetail: params => Http.get(`/access/api/v1/accounts/${params.id}`, { params }),
  getAccountDelete: data => Http.post(`/access/api/v1/accounts/delete`, data),
  getAccountEditor: data => Http.post(`/access/api/v1/accounts/password`, data)
}
